class Letter extends React.Component {
    render() {
      var letterStyle = {
          padding: 10,
          margin: 10,
          backgroundColor: this.props.bgcolor,
          color: this.props.color,
          display: "inline-block",
          fontFamily: "monospace",
          fontSize: 32,
          textAlign: "center"
        };

      return(
        <div style={letterStyle}>
          {this.props.children}
        </div>
      );
    }
  }

 
